import React from 'react';
import clsx from 'clsx';
import { withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DashboardIcon from '@material-ui/icons/Dashboard';
import MailIcon from '@material-ui/icons/Mail';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
const drawerWidth = 240;


const IOSSwitch = withStyles(theme => ({
    root: {
        width: 65,
        height: 43,
        padding: 0,
        margin: 1,
    },
    switchBase: {
        padding: 1,
        '&$checked': {
            transform: 'translateX(25px)',
            color: theme.palette.common.white,
            '& + $track': {
                backgroundColor: '#52d869',
                opacity: 1,
                border: 'none',
            },
        },
        '&$focusVisible $thumb': {
            color: '#52d869',
            border: '6px solid #fff',
        },
    },
    thumb: {
        width: 24,
        height: 24,
    },
    label: {
        justifyContent: 'center'
    },
    track: {
        borderRadius: 26 / 2,
        height: 20,
        width: 45,
        border: `1px solid ${theme.palette.grey[400]}`,
        backgroundColor: 'grey',
        opacity: 1,
        transition: theme.transitions.create(['background-color', 'border']),
    },
    checked: {},
    focusVisible: {},
}))(({ classes, ...props }) => {
    return (
        <Switch

            focusVisibleClassName={classes.focusVisible}
            classes={{
                root: classes.root,
                switchBase: classes.switchBase,
                iconChecked: classes.thumb,
                bar: classes.track,
                checked: classes.checked,

            }}
            {...props}
        />
    );
});

const styles = theme => ({
    root: {
        display: 'flex',
    },
    toolbar: {
        paddingRight: 24,
    },
    toolbarIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    menuButtonHidden: {
        display: 'none',
    },
    title: {
        flexGrow: 1,
    },
    drawerPaper: {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerPaperClose: {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        width: 7,
        [theme.breakpoints.up('sm')]: {
            width: 9,
        },
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    container: {
        paddingTop: 4,
        paddingBottom: 4,
    },
    paper: {
        padding: 2,
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 240,
    },
});
class Dashboard extends React.Component {
    state = {
        open: true,
        checkedA: false,
        checkedB: false,
        checkedC: false,
        email: false,
        facebook: false,
        sms: false,
        channels: true

    };

    handleChange = name => event => {
        this.setState({ ...this.state, [name]: event.target.checked });
    };
    handleDrawerOpen = () => {
        this.setState({ open: true });
    };
    handleDrawerClose = () => {
        this.setState({ open: false });
    };

    showFacebook = () => {
        this.setState({
            email: false,
            facebook: true,
            sms: false,
            channels: false
        });
    }
    showSms = () => {
        this.setState({
            email: false,
            facebook: false,
            sms: true,
            channels: false
        });
    }
    showEmail = () => {
        this.setState({
            email: true,
            facebook: false,
            sms: false,
            channels: false
        });
    }
    showChannels = () => {
        this.setState({
            email: false,
            facebook: false,
            sms: false,
            channels: true
        });
    }

    render() {
        const { classes, showDashboardPage } = this.props;
        const { open, checkedA, checkedB, checkedC, email, facebook, sms, channels } = this.state;



        return (
            <div className={classes.root} >
                <CssBaseline />
                <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)}>
                    <Toolbar className={classes.toolbar}>
                        <IconButton
                            edge="start"
                            color="inherit"
                            aria-label="Open drawer"
                            onClick={this.handleDrawerOpen}
                            className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
                        >
                            <MenuIcon />
                        </IconButton>
                        <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
                            Dashboard
          </Typography>

                    </Toolbar>
                </AppBar>
                <Drawer
                    variant="permanent"
                    classes={{
                        paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
                    }}
                    open={open}
                >
                    <div className={classes.toolbarIcon}>
                        <IconButton onClick={this.handleDrawerClose}>
                            <ChevronLeftIcon />
                        </IconButton>
                    </div>
                    <Divider />
                    <List>



                        <ListItem button onClick={() => { showDashboardPage(false) }}>
                            <ListItemIcon>
                                <DashboardIcon />
                            </ListItemIcon>
                            <ListItemText primary="Products" />
                        </ListItem>


                        <ListItem selected={channels} button onClick={this.showChannels}>
                            <ListItemIcon>
                                <BarChartIcon />
                            </ListItemIcon>
                            <ListItemText primary="Channels" />
                        </ListItem>
                        {checkedA ? <ListItem selected={email} button onClick={this.showEmail}>
                            <ListItemIcon>
                                <MailIcon />
                            </ListItemIcon>
                            <ListItemText primary="Email" />
                        </ListItem> : null}
                        {checkedB ? <ListItem selected={sms} button onClick={this.showSms}>
                            <ListItemIcon>
                                <BarChartIcon />
                            </ListItemIcon>
                            <ListItemText primary="SMS" />
                        </ListItem> : null}
                        {checkedC ? <ListItem selected={facebook} button onClick={this.showFacebook}>
                            <ListItemIcon>
                                <PeopleIcon />
                            </ListItemIcon>
                            <ListItemText primary="Facebook" />
                        </ListItem> : null}
                    </List>


                </Drawer>
                <main className={classes.content}>
                    <div className={classes.appBarSpacer} />
                    <Grid container className={classes.container}>


                        {channels && (<Grid container spacing={8}>

                            <Grid item xs={12} md={12} lg={12}>

                                <Grid container direction="row"
                                    justify="center"
                                    alignItems="center" spacing={24}>

                                    <Grid item xs={12} md={4} lg={4}>
                                        <Paper style={{ backgroundColor: '#E91E63' }}>
                                            <FormGroup className='switchIOS'>
                                                <FormControlLabel
                                                    control={
                                                        <IOSSwitch
                                                            checked={checkedA}
                                                            onChange={this.handleChange('checkedA')}
                                                            value="checkedA"
                                                        />
                                                    }
                                                    label="Email"
                                                />
                                            </FormGroup>
                                        </Paper>
                                    </Grid>


                                    <Grid item xs={12} md={4} lg={4}>
                                        <Paper style={{ backgroundColor: '#00BCD4' }}>        <FormGroup className='switchIOS'>
                                            <FormControlLabel
                                                control={
                                                    <IOSSwitch
                                                        checked={checkedB}
                                                        onChange={this.handleChange('checkedB')}
                                                        value="checkedB"
                                                    />
                                                }
                                                label="SMS"
                                            />
                                        </FormGroup>   </Paper>
                                    </Grid>


                                    <Grid item xs={12} md={4} lg={4}>
                                        <Paper style={{ backgroundColor: '#FF9800' }}>  <FormGroup className='switchIOS'>
                                            <FormControlLabel
                                                control={
                                                    <IOSSwitch
                                                        checked={checkedC}
                                                        onChange={this.handleChange('checkedC')}
                                                        value="checkedC"
                                                    />
                                                }
                                                label="Facebook"
                                            />
                                        </FormGroup>
                                        </Paper>
                                    </Grid>


                                </Grid>
                            </Grid>

                        </Grid>)}

                        <Grid container spacing={8}>

                            <Grid item xs={12} md={12} lg={12}>
                                {email && (<div><iframe src="https://emailserviceapp.azurewebsites.net/?id=5599" title="iframe" width="100%" height="900" /></div>)}
                                {facebook && (<div>Facebook</div>)}
                                {sms && (<div>SMS</div>)}
                            </Grid>

                        </Grid>


                    </Grid>

                </main>
            </div>
        );
    }
}

export default withStyles(styles)(Dashboard);
