import React from 'react';
import Products from './Products';
import SignIn from './SignIn';
import SignUp from './SignUp';
import Checkout from './Checkout';
import Dashboard from './Dashboard';
class App extends React.Component {
    state = {
        showProducts: false,
        itemsAdded: [],
        checkout: false,
        count: 0,
        signUp: false,
        showDashboard: false
    };

    showProductsPage = (show) => {
        this.setState({ showProducts: show })
    }
    showDashboardPage = (show) => {
        this.setState({ showDashboard: show })
    }


    showCheckoutPage = (show) => {
        this.setState({ checkout: show })
    }

    showSignUp = (show) => {
        this.setState({ signUp: show })
    }
    addItems = (item) => {
        let { itemsAdded, count } = this.state;
        count = count + 1;
        let itemNotPresent = true;
        itemsAdded.forEach(i => {
            if (i.title === item.title) {
                i.count = i.count + 1;
                itemNotPresent = false;
            }
        });
        if (itemNotPresent) {
            item['count'] = 1;
            itemsAdded.push(item);
        }
        this.setState({
            itemsAdded,
            count
        });
    }
    render() {  
        const { showProducts, itemsAdded, checkout, count, signUp, showDashboard } = this.state;
        return (
            <div>
                {showDashboard ? <Dashboard showDashboardPage={this.showDashboardPage}/> :
                    showProducts
                        ? checkout
                            ? <Checkout showCheckoutPage={this.showCheckoutPage} itemsAdded={itemsAdded} />
                            : <div><Products itemsAdded={itemsAdded} showDashboardPage={this.showDashboardPage} count={count} showCheckoutPage={this.showCheckoutPage} addItems={this.addItems} /></div>
                        : <div style={{ transform: 'translate(0%,50%)' }}>
                            {signUp ? <SignUp showSignUp={this.showSignUp} />
                                : <SignIn showSignUp={this.showSignUp} showProductsPage={this.showProductsPage} />
                            }

                        </div>}
            </div>

        );
    }
}

export default App;
